h1, p {
  font-family: Lato;
}
* {
  box-sizing: border-box; 
}
body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  align-items: center;
  justify-content: center;
}
#root {
  display: flex;
  flex: 1;
}
.header, footer {
	background: yellowgreen;
    height: 10vh;
    font-size: calc(10px + 2vmin);
    color: white;
    width: 100vw;
    text-align: center;
    padding: 0.9em;
}
.main{
	height:80vh;
	width: 100vw;
	background-color: beige;
	padding:2rem;
}
.Search{
	position: relative;
    left: 33%;
    width: 30vw;
}
.search-box {
    border: 0.2rem solid black;
    border-radius: 2px;
    font-size: 2rem;
    width: 100%;
    padding: 0.2rem;
    text-align: center;
}
.dropdown-menu{
	position: absolute;
    transform: translate3d(0, 3rem, 0);
    top: 0px;
    left: 33%;
    will-change: transform;
	display: block;
    float: left;
    width: 30vw;
    padding: .5rem 0;
    margin: .125rem 0 0;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: .25rem;
}
.dropdown-item{
	display: block;
    width: 100%;
    padding: .25rem 1.5rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}
.dropdown-item:hover{
	font-weight: bold;
	color: #00b4cc;
	cursor: pointer;
	transition: 0.3s all;
}
.option-active{
	background: whitesmoke;
	font-size: 1.5rem;
	color: #00b4cc;
}
@media screen and (max-width: 575px) {
  #root {
    display: block;
  }
}